      <Button clear={!(current === 'ota')} primary={current === 'ota'} disabled={diss} class="is-full-width" on:click={() => go('ota')}>OTA Update</Button><br />
      <Button clear={!(current === 'log')} primary={current === 'log'} disabled={diss} class="is-full-width" on:click={() => go('log')}>Логи</Button><br />
    </div>
  {/if}

  <Card class="bg-light" style={!miniWindow ? 'margin-left: 150px; padding: 15px; height: auto;' : 'margin: 0; padding: 15px; height: auto;'}>
    <!--DIV -->
    {#if current === 'main'}
      <RangeSlider formatter={(v) => v / 60} handleFormatter={(v) => min_to_hr(v)} range={true} pips pipstep={24} all="label" float hover={true} bind:values={ranger} min={0} max={1440} step={5} />
      Восхо
      {min_to_hr(ranger[0])}<br />
      Заход
      {min_to_hr(ranger[1])}<br />
      Время освещения
      {min_to_hr(ranger[1] - ranger[0])}<br />
      Главная{metric.length}{width_win}<br />
      {#each Object.keys(metric) as keys}
        <h3>
          {#if metric[keys].icon == 'temp'}
            <Icon src={temp} size="1.2" />
          {:else if metric[keys].icon == 'humi'}
            <Icon src={humi} size="1.2" />
          {:else if metric[keys].icon == 'level'}
            <Icon src={lvl} size="1.2" />
          {:else if metric[keys].icon == 'icon'}
            <Icon src={temp} size="1" />
          {:else if metric[keys].icon == 'icon'}
            <Icon src={temp} size="1" />
          {:else if metric[keys].icon == 'icon'}
            <Icon src={temp} size="1" />
          {:else if metric[keys].icon == 'icon'}
            <Icon src={temp} size="1" />
          {/if}
          {metric[keys].name}
          {metric[keys].value}
          {metric[keys].postfix}
        </h3>
      {/each}
    {:else if current === 'sett'}
      Настройки твоего приложения
    {:else if current === 'wifi'}
