          <Checkbox name="n3" value="first" />
          <Checkbox name="n4" value="second" />

          <Input name="pass" success placeholder="Имя точки доступа" />
          <Input name="login" placeholder="пароль" />
        </Field>
      {:else}
        <Field grouped label="" id="wifi">
          <Input class="is-hidden" name="mode" value={mode_wifi} />
          <Input name="pass" success placeholder="Имя точки доступа" />
          <Input name="login" placeholder="пароль" />
        </Field>
        <h4>Подключен к точке {current}</h4>
      {/if}
