        {#each Object.entries(wifi_scan) as [key, value]}
          <tr
            id="wifi_tb"
            on:mouseenter={(e) => (button_text = true)}
            on:mouseleave={(e) => (button_text = false)}
            on:click={() => {
              //simpleDialog.open();
              openn = true;
              clickedd = key;
              if (value.secure == 'close') {
                diss_pass = false;
              } else {
                diss_pass = true;
              }
            }}>
            <td align="left">{key}</td>
            <!--<td>{value.channel}</td>-->
            <td>
              {#if value.rssi > -50}
                <Icon src={wifi4} size="1.2" />
              {:else if value.rssi > -50}
                <Icon src={wifi3} size="1.2" />
              {:else if value.rssi > -70}
                <Icon src={wifi2} size="1.2" />
              {:else if value.rssi > -100}
                <Icon src={wifi1} size="1.2" />
              {/if}
            </td>
            <td>
              {#if value.secure == 'close'}
                <Icon src={icclose} size="1.2" />
              {:else}
                <Icon src={icopen} size="1.2" />
              {/if}
            </td>
            <td>
              {#if value.hide}
                <Icon src={ichide} size="1.2" />
              {:else}
                <Icon src={icunhide} size="1.2" />
              {/if}
            </td>
          </tr>

          <!-- <Row
              class="bg-primary"
              on:click={() => {
                //simpleDialog.open();
                openn = true;
                clickedd = key;
              }}>
              <Col>{key}</Col>
