        {/each}
        <tr>
          <td colspan="5" style="text-align:right">Сейчас подключено</td>
        </tr>
      </table>
    {:else if current === 'mqtt'}
      {current}
      <Field id="mqtt">
        <Input name="login" placeholder="Your name?" />
        <Input name="passec" password placeholder="Your password?" />
      </Field>
      <Details>
        <span slot="summary">Подробнее</span>
        <b>MQTT </b>
        или Message Queue Telemetry Transport – это легкий, компактный и открытый протокол обмена данными созданный для передачи данных на удалённых локациях, где требуется небольшой размер кода и есть ограничения по пропускной способности канала. Вышеперечисленные достоинства позволяют применять
        его в системах M2M (Машинно-Машинное взаимодействие) и IIoT (Промышленный Интернет вещей).
        <a href="https://grafana.com/">Grafana</a>.
        <a href="https://tlgrm.ru/docs/bots/api">Справочник</a>
      </Details>
