      <Input placeholder="API ID" on:keypress={() => sms_ru(sms_status.length)} bind:value={sms_status} />
      {sms_status}
      <Details><span slot="summary">Подробнее</span> <b>SMS.RU</b> — это сервис СМС-рассылки<br /> Для разработчиков до 5 смс на свой номер бесплатно.<br /> <a href="https://sms.ru/?panel=register">регистрация</a>.</Details>
    {:else if current === 'flux'}
      <Field grouped label="Login & Passw">
        <Input placeholder="Your name?" />
        <Input password placeholder="Your password?" />
      </Field>
      <Field grouped label="Server & Port">
        <Input style="width: 400%" placeholder="Your server" />
        <Input onkeyup="this.value=parseInt(this.value) | 0" placeholder=":port" />
