        <span slot="summary">Что это такое?</span>
        <b>InfluxDB</b>
        - это база данных временных рядов, предназначенная для обработки высокой нагрузки записи и запросов. Основным назначением является хранение больших объемов данных с метками времени. Например, данные мониторинга, метрики приложений и данные датчиков IoT. Очень удобно отображать всю метрику
        через
        <a href="https://grafana.com/">Grafana</a>.
        <a href="https://tlgrm.ru/docs/bots/api">Справочник</a>
      </Details>
